import Frame from "~/components/Frame2";
import BarChartHorizontal from "~/components/BarChartHorizontal";
import BarChartVertical from "~/components/BarChartVertival";
import Maps from "~/components/Maps";
import DetailBuilding from "~/components/DetailBuilding.vue";
import moment from "moment";

export default {
  layout: "main",
  components: {
    Frame,
    BarChartHorizontal,
    BarChartVertical,
    Maps,
    DetailBuilding,
  },
  data() {
    return {
      time2: "",
      dialog: "true",
      total: "",
    };
  },
  methods: {
    getTime: function () {
      let gettime = new Date();
      this.time2 = moment(gettime).format("H:mm:ss");
    },
    getData: function () {
      this.$axios
        // .$get("http://localhost:7777/api/total", {})
        .$get("http://165.232.167.69/api/total", {})
        .then((response) => {
          // console.log(response);
          this.total = response.total;
          // window.location.reload();
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    setInterval(() => {
      this.getTime();
    }, 1000);
    this.getData();
  },
  computed: {
    height() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return true;
        case "sm":
          return true;
        case "md":
          return true;
        case "lg":
          return false;
        case "xl":
          return false;
      }
    },
  },
};
